export const createDomAnchor = (anchorId: string) => {
    const anchor = document.createElement('div');
    anchor.id = anchorId;
    document.body.insertBefore(anchor, document.body.childNodes[0]);
};

export const createPortalContainer = (portalId: string): HTMLElement => {
    const portal = document.createElement('div');
    portal.setAttribute('maf-page-name', portalId);
    portal.id = portalId;
    portal.style.position = 'absolute';
    portal.style.zIndex = '10';
    portal.style.right = '100px';
    portal.style.top = '5px';
    return portal;
};
