import * as React from 'react';
import { useSelector, useDispatch } from 'react-redux';

import { IAppState } from 'ts/background/store';
import { addAds, updateAd } from 'ts/background/store/ads';
import { getPageNameFromElement, isAd } from 'ts/scripts/facebookUtils';
import { IAd } from 'ts/background/models';
import {
    FirstRunMessage,
    getInitialData,
    getTopLevelObjectsFromString,
    parseAds,
    parseWatchAds,
    randomInteger,
} from 'ts/scripts/utils';
import { getWindowMessageNames, TRAFFIC_ID } from 'ts/scripts/messageUtils';
import { createPortalContainer } from 'ts/scripts/dom';
import { debug, log } from 'ts/scripts/logger';
import {
    trackEvent,
    disableFirstRun,
    enableAutoScroll,
    disableAutoScroll,
} from 'ts/background/store/settings';
import FirstRunModal from 'ts/components/firsRunModal';
import { Portal } from './Portal';

declare global {
    interface Window {
        _feedMutationObserverStarted: boolean;
        requestIdleCallback: (
            callback: IdleRequestCallback,
            options?: IdleRequestOptions | undefined,
        ) => number;
    }
}

let localAds: {
    [key: string]: IAd;
} = {};

let domChangeObserver: MutationObserver;

let scrolldelay: number;
const REFRESH_TIMER = 45 * 1000; // 45 seconds
const REFRESH_TIMER_FIRST_RUN = 20 * 1000; // 20 seconds

const addNodes = (
    addedElements: HTMLElement[],
    organicPosts: boolean,
    setPortals: React.Dispatch<React.SetStateAction<HTMLElement[]>>,
) => {
    log(addedElements, 'addedElements');
    for (const addedElement of addedElements) {
        log(addedElement, 'addedElement');
        try {
            const pageName = getPageNameFromElement(addedElement);
            if (pageName === '') {
                if (!organicPosts) {
                    addedElement.style.display = 'none';
                }
                return;
            }
            debug(pageName, 'pageName');
            debug(localAds);
            debug(!!localAds[pageName]);
            debug('--------------');
            if (
                isAd(addedElement) ||
                !!localAds[pageName] ||
                Object.keys(localAds).some((k) => pageName.includes(k))
            ) {
                delete localAds[pageName];
                const { firstChild } = addedElement;
                if ((firstChild as HTMLElement).getAttribute('maf-page-name') === pageName) {
                    return;
                }
                addedElement.style.display = 'block';
                addedElement.style.position = 'relative';
                const portal = createPortalContainer(pageName);
                addedElement.insertBefore(portal, firstChild);
                setPortals((portals) => [...portals, portal]);
            } else {
                if (organicPosts) {
                    addedElement.style.display = 'block';
                } else {
                    addedElement.style.display = 'none';
                }
            }
        } catch (error) {
            debug(error, 'addNodesError');
        }
    }
};

const mutationCallback =
    (organicPosts: boolean, setPortals: React.Dispatch<React.SetStateAction<HTMLElement[]>>) =>
    (mutations: MutationRecord[]) =>
        mutations.forEach(({ addedNodes }) => {
            const elements = (Array.from(addedNodes) as HTMLElement[]).filter(
                (node) => node.childNodes.length > 0,
            );
            if (elements.length > 0) {
                addNodes(elements, organicPosts, setPortals);
            }
        });

const createPageTrafficListener = (id: string, callback: (newAds: IAd[]) => void) => {
    const { message_id } = getWindowMessageNames(id);
    const handleMessage = (message: any) => {
        if (message.origin.indexOf('facebook.com') > -1 && message.data) {
            if (message.data.type === message_id) {
                const allData = getTopLevelObjectsFromString(message.data.payload);
                const ads = parseAds(allData);
                if (ads.length > 0) {
                    localAds = {
                        ...localAds,
                        ...ads.reduce(
                            (acc, ad: IAd) => ({
                                ...acc,
                                [ad.pageName]: ad,
                            }),
                            {},
                        ),
                    };
                    callback(ads);
                }
            } else if (message.data.type === `${message_id}_watch`) {
                const allData = getTopLevelObjectsFromString(message.data.payload);
                const watchAds = parseWatchAds(allData);
                if (watchAds.length > 0) {
                    localAds = {
                        ...localAds,
                        ...watchAds.reduce(
                            (acc, ad: IAd) => ({
                                ...acc,
                                [ad.pageName]: ad,
                            }),
                            {},
                        ),
                    };
                    callback(watchAds);
                }
            }
        }
    };

    window.addEventListener('message', handleMessage);
};

const handleInitialAds = (callback: (ads: IAd[]) => void) => {
    try {
        const intialData = getInitialData(document.body);
        const initialAds = parseAds(intialData);
        localAds = {
            ...localAds,
            ...initialAds.reduce(
                (acc, ad: IAd) => ({
                    ...acc,
                    [ad.pageName]: ad,
                }),
                {},
            ),
        };
        callback(initialAds);
    } catch (err) {
        log(err, 'handleInitialAds');
    }
};

const getTargetNode = (): Element => {
    if (window.location.href.includes('facebook.com/watch')) {
        return document.querySelector('[id="watch_feed"] > div > div > div > div > div') as Element;
    }
    if (document.querySelector('[class=x1lliihq]')?.parentElement) {
        return document.querySelector('[class=x1lliihq]')?.parentElement as Element;
    }
    if (document.querySelector('[class=b6ax4al1]')?.parentElement) {
        return document.querySelector('[class=b6ax4al1]')?.parentElement as Element;
    }
    const feedStart = document.querySelector('[id="ssrb_feed_start"]')?.parentElement;
    const feedFirstDiv = feedStart?.querySelector('div');
    const feed = feedFirstDiv?.querySelector('div');
    if (feed) {
        return feed;
    }
    return document.querySelector('[role="feed"] > div') as Element;
};

const ContentScriptApp = () => {
    const dispatch = useDispatch();
    const ads = useSelector((state: IAppState) => state.ads);
    const settings = useSelector((state: IAppState) => state.settings);
    const [portals, setPortals] = React.useState<HTMLElement[]>([]);
    const [recentAds, setRecentAds] = React.useState<Record<string, IAd>>();
    const [heightState, setHeightState] = React.useState<{ height: number; date: Date }>({
        height: 0,
        date: new Date(),
    });
    const [favoritesMap, setFavoritesMap] = React.useState<Record<string, boolean>>(
        ads.data.reduce(
            (map: Record<string, boolean>, storeAd: IAd) => ({
                ...map,
                [storeAd.postId]: storeAd.favorite || false,
            }),
            {},
        ),
    );
    const [firstRunProcessed, setFirstRunProcessed] = React.useState<boolean>(!settings.firstRun);
    const [firstRunClone] = React.useState<boolean>(settings.firstRun);
    const [firstRunMessageId, setfirstRunMessageId] = React.useState<FirstRunMessage>(
        FirstRunMessage.SEEKING,
    );

    const handleNewAds = React.useCallback((newAds: IAd[]) => {
        dispatch(addAds(newAds));
        setRecentAds((currentAds) => {
            return {
                ...currentAds,
                ...newAds.reduce(
                    (map, currentAd) => ({
                        ...map,
                        [currentAd.pageName]: currentAd,
                    }),
                    {},
                ),
            };
        });
    }, []);

    React.useEffect(() => {
        createPageTrafficListener(TRAFFIC_ID, handleNewAds);
        handleInitialAds(handleNewAds);
        runTheFistAdsSeeking();
    }, []);

    React.useEffect(() => {
        let observer: MutationObserver;
        try {
            let lastUrl = location.href;

            const observerOptions = { subtree: true, childList: true };

            observer = new MutationObserver(() => {
                const url = location.href;
                if (url !== lastUrl) {
                    if (url.includes('facebook.com')) {
                        window._feedMutationObserverStarted = false;
                        window.requestIdleCallback(checkForFeedElementObserver);
                    }
                    lastUrl = url;
                }
            });

            observer.observe(document, observerOptions);
        } catch (err) {
            log(err, 'useEffect/MutationObserver');
        }
        return () => {
            debug('discarding route change observer...');
            observer?.disconnect();
        };
    }, []);

    React.useEffect(() => {
        if (!firstRunProcessed && ads.data.length > 0) {
            setfirstRunMessageId(FirstRunMessage.CONGRATULATIONS);
            setFirstRunProcessed(true);
        }
        const newClientHeight = getTargetNode()?.clientHeight;
        if (
            !firstRunProcessed &&
            new Date().getTime() > heightState.date.getTime() + REFRESH_TIMER_FIRST_RUN
        ) {
            // set message and let user change the account
            dispatch(disableAutoScroll());
            setfirstRunMessageId(FirstRunMessage.UNFORTUNATELY);
        } else if (
            heightState.height === newClientHeight &&
            new Date().getTime() > heightState.date.getTime() + REFRESH_TIMER
        ) {
            clearTimeout(scrolldelay);
            window.location.reload();
        }
    }, [heightState]);

    React.useEffect(() => {
        if (settings.autoScroll) {
            pageScroll()();
        } else {
            clearTimeout(scrolldelay);
        }

        return () => {
            clearTimeout(scrolldelay);
        };
    }, [settings.autoScroll]);

    React.useEffect(() => {
        if (firstRunProcessed) {
            clearTimeout(scrolldelay);
        }
        return () => {
            clearTimeout(scrolldelay);
        };
    }, [firstRunProcessed]);

    React.useEffect(() => {
        createDomOnChangeListener();
        return () => {
            debug('disconnecting observer');
            domChangeObserver?.disconnect();
        };
    }, []);

    const setFavorite = React.useCallback((ad: IAd, favorite: boolean) => {
        setFavoritesMap((fMap) => ({
            ...fMap,
            [ad.postId]: favorite,
        }));
        dispatch(updateAd({ ...ad, favorite }));
        dispatch(
            trackEvent({
                category: 'CS',
                action: 'favorite',
            }),
        );
    }, []);

    const handleALClick = React.useCallback((adLibraryId: string) => {
        window.open(
            `https://www.facebook.com/ads/library/?active_status=all&ad_type=all&country=ALL&view_all_page_id=${adLibraryId}&search_type=page&media_type=all`,
            '_blank',
        );
        dispatch(
            trackEvent({
                category: 'CS',
                action: 'ad_library',
            }),
        );
    }, []);

    const checkForFeedElementObserver = React.useCallback(() => {
        if (!window._feedMutationObserverStarted) {
            createDomOnChangeListener();

            window.requestIdleCallback(checkForFeedElementObserver);
        }
    }, []);

    const createDomOnChangeListener = React.useCallback(() => {
        try {
            const targetNode = getTargetNode();

            log(targetNode, 'targetNode');

            if (targetNode) {
                const observerOptions = {
                    childList: true,
                    attributes: false,
                    subtree: false,
                };

                domChangeObserver = new MutationObserver(
                    mutationCallback(settings.organicPosts, setPortals),
                );

                window._feedMutationObserverStarted = true;

                addNodes(
                    Array.from(targetNode.childNodes) as HTMLElement[],
                    settings.organicPosts,
                    setPortals,
                );

                domChangeObserver.observe(targetNode, observerOptions);
            } else {
                if ('requestIdleCallback' in window) {
                    window.requestIdleCallback(checkForFeedElementObserver);
                } else {
                    setTimeout(() => checkForFeedElementObserver(), 300);
                }
            }
        } catch (err) {
            log(err, 'createDomOnChangeListener');
        }
    }, []);

    const runTheFistAdsSeeking = (): void => {
        if (settings.firstRun) {
            dispatch(disableFirstRun());
            if (ads.data.length > 0) {
                setFirstRunProcessed(true);
            } else {
                dispatch(enableAutoScroll());
            }
        } else if (!settings.firstRun && ads.data.length > 0) {
            dispatch(disableAutoScroll());
        }
    };

    const pageScroll = React.useCallback(
        (down = true) =>
            () => {
                const newClientHeight = getTargetNode()?.clientHeight;
                setHeightState(({ height, date }) => ({
                    height: newClientHeight,
                    date: height !== newClientHeight ? new Date() : date,
                }));
                window.scrollBy(0, down ? randomInteger(900, 1100) : -randomInteger(200, 300));
                scrolldelay = window.setTimeout(pageScroll(!down), randomInteger(800, 1200));
            },
        [],
    );

    return (
        <>
            {firstRunClone && <FirstRunModal messageId={firstRunMessageId} />}
            {portals
                .map((portal) => ({
                    ad: recentAds?.[portal.id] as IAd,
                    id: portal.id,
                    portal,
                }))
                .filter(({ ad }) => !!ad)
                .map(({ ad, portal, id }, index) => (
                    <Portal
                        key={`${id}_${index}`}
                        element={portal}
                        ad={{
                            ...ad,
                            favorite: favoritesMap[ad.postId] || false,
                        }}
                        setFavorite={setFavorite}
                        handleALClick={handleALClick}
                    />
                ))}
        </>
    );
};

export default ContentScriptApp;
