import { log, debug } from './logger';

export const getPageNameFromElement = (element: Element): string => {
    try {
        try {
            const anchorsWithAriaLabel = element.querySelectorAll('a[aria-label]') || [];
            const pageName = anchorsWithAriaLabel[0]?.getAttribute('aria-label');
            if (pageName) {
                return pageName;
            }
        } catch {
            //
        }

        if (window.location.href.includes('facebook.com/watch')) {
            const a = (element.querySelector('[class="xh8yej3"] a') ||
                element.querySelector(
                    'div > div > div > div > div.k4urcfbm > div.j83agx80.cbu4d94t.buofh1pr.kvgmc6g5.tvfksri0.oygrvhab.ozuftl9m.ihqw7lf3 > div.rq0escxv.l9j0dhe7.du4w35lb.j83agx80.pfnyh3mw.i1fnvgqd.bp9cbjyn.owycx6da.btwxx1t3.discj3wi.b5q2rw42.lq239pai.mysgfdmx.hddg9phg > div.rq0escxv.l9j0dhe7.du4w35lb.j83agx80.cbu4d94t.pfnyh3mw.d2edcug0.hpfvmrgz.p8fzw8mz.pcp91wgn.r8blr3vg.jwdofwj8 > a',
                )) as HTMLAnchorElement;
            log(a, 'watchPageNameTag');
            return a?.getAttribute('aria-label') || '';
        } else {
            const [companyElement] = Array.from(
                element.querySelectorAll('[id^="jsc_c"] a'),
            ) as HTMLAnchorElement[];
            return companyElement?.textContent || '';
        }
    } catch {
        return '';
    }
};

