let adFinder: ((element: Element, balise: string) => boolean) | ((element: Element) => boolean);
let baliseFinder: string;

export const isAd = (element: Element, refreshBalise = false) => {
    try {
        if (adFinder && !refreshBalise) {
            return adFinder(element, baliseFinder);
        }

        return (
            isNewAd3(element) ||
            isNewAd4(element) ||
            isNewAd1(element, 'a.oajrlxb2 > span > *.nc684nl6') ||
            isNewAd1(element, 'div.oajrlxb2 > span > *.nc684nl6') ||
            isNewAd1(element, 'a.oajrlxb2 > span > span > *.nc684nl6') ||
            isNewAd1(element, 'div.oajrlxb2 > span > span > *.nc684nl6') ||
            isNewAd1(element, 'a.oajrlxb2 > span > span > span > *.nc684nl6') ||
            isNewAd1(element, 'div.oajrlxb2 > span > span > span > *.nc684nl6') ||
            isNewAd2(element)
        );
    } catch (err) {
        log(err, 'isAd');
        return false;
    }
};

const isNewAd3 = (element: Element) => {
    debug('isNewAd3');
    try {
        const elementsWithSponsoredAriaLabel = Array.from(
            element.querySelectorAll('a[aria-label]'),
        ).find((e) => {
            const word = e.getAttribute('aria-label')?.toLowerCase();
            debug(word, 'isNewAd3');
            if (!word) {
                return false;
            }
            return adWordsSet.has(word);
        });

        if (!elementsWithSponsoredAriaLabel) {
            return false;
        }
        adFinder = isNewAd3;
        return true;
    } catch (err) {
        log(err, 'isNewAd3');
        return false;
    }
};

const isNewAd4 = (element: Element) => {
    debug('isNewAd4');
    try {
        const elementWithAdsAbout = element.querySelector('a[href^="/ads/about"]');
        debug(elementWithAdsAbout, 'isNewAd4');
        if (!elementWithAdsAbout) {
            return false;
        }
        adFinder = isNewAd4;
        return true;
    } catch (err) {
        log(err, 'isNewAd4');
        return false;
    }
};

const getSponsored = (newSpans: HTMLElement[]): string =>
    newSpans
        .filter((span) => {
            try {
                return (
                    span.style.position !== 'absolute' &&
                    span.style.display !== 'none' &&
                    getComputedStyle(span).position !== 'absolute' &&
                    getComputedStyle(span).display !== 'none'
                );
            } catch (err) {
                log(err, 'getSponsored');
                return false;
            }
        })
        .reduce((acc1, curr1) => {
            const children = Array.from(curr1.childNodes);
            return (
                acc1 +
                children.reduce((acc2, curr2) => {
                    if (curr2.nodeType === Node.TEXT_NODE) {
                        return acc2 + curr2.nodeValue;
                    } else if (curr2.nodeType === Node.ELEMENT_NODE) {
                        return acc2 + getSponsored([curr2 as HTMLElement]);
                    }
                    return acc2;
                }, acc1)
            );
        }, '')
        .toLowerCase();

const isNewAd1 = (element: Element, selector: string) => {
    debug('isNewAd1');
    try {
        const spans = Array.from(element.querySelectorAll(selector)) as HTMLSpanElement[];
        const acc = getSponsored(spans);
        if (adWordsSet.has(acc)) {
            baliseFinder = selector;
            adFinder = isNewAd1;
            return true;
        }
        return false;
    } catch (err) {
        log(err, 'isNewAd1');
        return false;
    }
};

const isNewAd2 = (element: Element) => {
    debug('isNewAd2');
    try {
        const acc = (Array.from(element.querySelectorAll('[data-content]')) as HTMLElement[])
            .filter((e) => !!e.getAttribute('data-content') && e.style.display !== 'none')
            .map((e) => e.getAttribute('data-content'))
            .join('')
            .toLowerCase();

        if (adWordsSet.has(acc)) {
            adFinder = isNewAd2;
            return true;
        }
    } catch (err) {
        log(err, 'isNewAd2');
    }
    return false;
};
