import * as React from 'react';
import ReactDOM from 'react-dom';
import { IconButton, Button } from '@mui/material';
import { faHeart as faHeartSolid } from '@fortawesome/free-solid-svg-icons';
import { faHeart as faHeartRegular } from '@fortawesome/free-regular-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

import { IAd } from 'ts/background/models';

type PortalProps = {
    element: HTMLElement;
    ad: IAd;
    setFavorite: (ad: IAd, favorite: boolean) => void;
    handleALClick: (adLibraryId: string) => void;
};

export const Portal = ({ element, ad, setFavorite, handleALClick }: PortalProps) => {
    if (!ad) {
        return null;
    }

    const onButtonClick = () => setFavorite(ad, !ad.favorite);

    const onALClick = () => handleALClick(ad.adLibraryId);

    return ReactDOM.createPortal(
        <>
            <Button variant="outlined" color="primary" size="small" onClick={onALClick}>
                Ad Library
            </Button>
            <IconButton aria-label="favorite-ad" onClick={onButtonClick} color="secondary">
                <FontAwesomeIcon icon={ad.favorite ? faHeartSolid : faHeartRegular} />
            </IconButton>
        </>,
        element,
    );
};
