import * as React from 'react';
import ReactDOM from 'react-dom';
import { Grid, CircularProgress, Typography, Box } from '@mui/material';
import { FormattedMessage } from 'react-intl';
import { FirstRunMessage } from 'ts/scripts/utils';

interface IFirstRunModalProps {
    messageId: string;
}

const FirstRunModal: React.FunctionComponent<IFirstRunModalProps> = ({ messageId }) => {
    const element = document.querySelector('body') as Element;
    const [open, setOpen] = React.useState(true);

    React.useEffect(() => {
        setOpen(true);
    }, [messageId]);

    const handleClose = () => {
        setOpen(false);
    };

    if (open) {
        return ReactDOM.createPortal(
            <Box
                sx={{
                    position: 'fixed',
                    zIndex: 1300,
                    inset: '0px',
                }}
            >
                <Box
                    sx={{
                        position: 'fixed',
                        inset: '0px',
                        backgroundColor: 'rgba(0,0,0,.5)',
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent: 'center',
                        zIndex: -1,
                    }}
                    onClick={handleClose}
                />

                <Grid
                    container={true}
                    sx={{
                        position: 'absolute',
                        top: '50%',
                        left: '50%',
                        backgroundColor: '#fff',
                        transform: 'translate(-50%, -50%)',
                        padding: '20px 0',
                        width: '450px',
                        justifyContent: 'center',
                        alignItems: 'center',
                        borderRadius: '20px',
                        overflow: 'hidden',
                    }}
                    direction="row"
                >
                    <img
                        style={{
                            maxWidth: '70%',
                            margin: 'auto',
                            display: 'block',
                        }}
                        alt="logo"
                        src={`${chrome.runtime.getURL('/assets/bleulogo.png')}`}
                    />
                    {messageId === FirstRunMessage.SEEKING ? (
                        <Grid item={true} xs={12}>
                            <Grid container={true} direction="column">
                                <CircularProgress sx={{ margin: 'auto', display: 'block' }} />
                                <Grid
                                    item={true}
                                    xs={12}
                                    sx={{
                                        marginTop: '12px',
                                    }}
                                >
                                    <Typography
                                        sx={{
                                            textAlign: 'center',
                                            margin: 'auto 15px',
                                        }}
                                    >
                                        <FormattedMessage
                                            id="app.first_run.modal.wait"
                                            defaultMessage="Please wait, searching for Ads..."
                                        />
                                    </Typography>
                                    <Typography
                                        sx={{
                                            textAlign: 'center',
                                            margin: 'auto 15px',
                                        }}
                                    >
                                        <FormattedMessage
                                            id="app.first_run.modal.complete"
                                            defaultMessage="To complete the ads search, keep this page active and visible."
                                        />
                                    </Typography>
                                </Grid>
                            </Grid>
                        </Grid>
                    ) : null}
                    {messageId === FirstRunMessage.CONGRATULATIONS ? (
                        <Grid item={true} xs={12}>
                            <Grid container={true} direction="column">
                                <img
                                    style={{
                                        maxWidth: '20%',
                                        margin: 'auto',
                                        display: 'block',
                                    }}
                                    alt={FirstRunMessage.CONGRATULATIONS}
                                    src={`${chrome.runtime.getURL('/assets/happy.png')}`}
                                />
                                <Grid
                                    item={true}
                                    sx={{
                                        marginTop: '12px',
                                    }}
                                >
                                    <Typography
                                        sx={{
                                            textAlign: 'center',
                                            margin: 'auto 15px',
                                        }}
                                    >
                                        <FormattedMessage
                                            id="app.first_run.modal.Congratulations"
                                            defaultMessage="Congratulations"
                                        />
                                    </Typography>
                                    <Typography
                                        sx={{
                                            textAlign: 'center',
                                            margin: 'auto 15px',
                                        }}
                                    >
                                        <FormattedMessage
                                            id="app.first_run.modal.use"
                                            defaultMessage="You can now use MY AD FINDER."
                                        />{' '}
                                    </Typography>
                                </Grid>
                            </Grid>
                        </Grid>
                    ) : null}
                    {messageId === FirstRunMessage.UNFORTUNATELY ? (
                        <Grid item={true} xs={12}>
                            <Grid container={true} direction="column">
                                <img
                                    style={{
                                        maxWidth: '20%',
                                        margin: 'auto',
                                        display: 'block',
                                    }}
                                    alt={FirstRunMessage.UNFORTUNATELY}
                                    src={`${chrome.runtime.getURL('/assets/sad.png')}`}
                                />

                                <Grid
                                    item={true}
                                    sx={{
                                        marginTop: '12px',
                                    }}
                                >
                                    <Typography
                                        sx={{
                                            textAlign: 'center',
                                            margin: 'auto 15px',
                                        }}
                                    >
                                        <FormattedMessage
                                            id="app.first_run.modal.unfortunately"
                                            defaultMessage="Unfortunately, no ads found. Please try to find the first ad manually. "
                                        />
                                    </Typography>
                                </Grid>

                                <Grid item={true}>
                                    <Grid container={true} sx={{ marginTop: '15px' }}>
                                        <Grid item={true} xs={3}>
                                            <img
                                                style={{
                                                    maxWidth: '50%',
                                                    float: 'right',
                                                }}
                                                src={`${chrome.runtime.getURL(
                                                    '/assets/freshchat.png',
                                                )}`}
                                            />
                                        </Grid>
                                        <Grid
                                            xs={9}
                                            item={true}
                                            sx={{
                                                display: 'flex',
                                                alignItems: 'center',
                                            }}
                                        >
                                            <Typography
                                                sx={{
                                                    textAlign: 'left',
                                                    marginLeft: '2px',
                                                }}
                                            >
                                                <FormattedMessage
                                                    id="app.first_run.modal.contact"
                                                    defaultMessage="contact us from Collected Ads page. "
                                                />
                                            </Typography>
                                        </Grid>
                                    </Grid>
                                </Grid>
                            </Grid>
                        </Grid>
                    ) : null}
                </Grid>
            </Box>,

            element,
        );
    } else {
        return null;
    }
};
export default FirstRunModal;
