import React from 'react';
import { createRoot } from 'react-dom/client';
import { Provider } from 'react-redux';
import { ThemeProvider } from '@mui/material/styles';
import { Store } from 'webext-redux';

import ContentScriptApp from './containers/ContentScriptApp';
import ConnectedIntlProvider from 'ts/containers/ConnectedIntlProvider';
import theme from 'ts/theme';
import { createDomAnchor } from 'ts/scripts/dom';

createDomAnchor('maf-cs-root');
const store = new Store();

store.ready().then(() => {
    const container = document.getElementById('maf-cs-root');

    if (!container) {
        return;
    }

    const root = createRoot(container);

    root.render(
        <Provider store={store}>
            <ConnectedIntlProvider>
                <ThemeProvider theme={theme}>
                    <ContentScriptApp />
                </ThemeProvider>
            </ConnectedIntlProvider>
        </Provider>,
    );
});
